html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
::after,
::before {
  box-sizing: inherit;
}

body {
  font-family: 'Montserrat', cursive, sans-serif;
  background: #4b4d66;
  text-align: center;
  max-width: 1040px;
  margin: 0 auto;
  padding: 0 20px;
}
